//https://material.angular.io/
import { NgModule }             from '@angular/core';
import {MdTabsModule
  ,MdIconModule
  ,MdToolbarModule
  ,MdButtonModule
  ,MdMenuModule
  ,MdInputModule
  ,MdGridListModule
  ,MdTableModule
  ,MdDatepickerModule
  ,MdNativeDateModule 
  
} from '@angular/material';

@NgModule({
  imports: [MdTabsModule
    ,MdIconModule
    ,MdToolbarModule
    ,MdButtonModule
    ,MdMenuModule
    ,MdInputModule
    ,MdGridListModule
    ,MdTableModule
    ,MdDatepickerModule
    ,MdNativeDateModule 
  ],

  exports: [MdTabsModule
    ,MdIconModule
    ,MdToolbarModule
    ,MdButtonModule
    ,MdMenuModule
    ,MdInputModule
    ,MdGridListModule
    ,MdTableModule
    ,MdDatepickerModule
    ,MdNativeDateModule 
  ],
})
export class AngularMaterialModule { }


